import { useMachine } from '@xstate/react';
import { Modal, Button } from 'antd';
import ReactPlayer from 'react-player';
import machine from './machine';

const App = () => {
  const [state, send] = useMachine(machine);
  
  const openModal = () => {
    send({ type: 'OPEN_MODAL' });
    setTimeout(() => {
      send({ type: 'PLAY' });
    }, 100);
  };

  const closeModal = () => {
    send({ type: 'CLOSE_MODAL' });
  };

  const handlePlayPause = () => {
    send({ type: state.matches('playing') ? 'PAUSE' : 'PLAY' });
  };

const handleResize = () => {
  const isFullScreen = state.context.modalWidth === 1280;
  const newWidth = isFullScreen ? 640 : 1280;
  const newHeight = isFullScreen ? 360 : 720;

  send({
    type: 'RESIZE',
    width: newWidth,
    height: newHeight,
  });
};

  const handleEnded = () => {
    send({ type: 'END' });
    setTimeout(() => {
      send({ type: 'PLAY' });
    }, 100);
  };

  return (
    <div>
      <Button type="primary" onClick={openModal}>
        Open Video Player
      </Button>

      <Modal
        title="PLAYER"
        open={state.context.isModalVisible}
        onCancel={closeModal}
        footer={null}
        width={state.context.modalWidth}
        height={state.context.modalHeight}
      >
        <ReactPlayer
          ref={state.context.videoRef}
          url="https://cdn.flowplayer.com/d9cd469f-14fc-4b7b-a7f6-ccbfa755dcb8/hls/383f752a-cbd1-4691-a73f-a4e583391b3d/playlist.m3u8"
          playing={state.matches('playing')}
          muted={true}
          width="100%"
          height="100%"
          onEnded={handleEnded}
        />
        <div
          style={{
            marginTop: '10px',
            display: 'flex',
            justifyContent: 'flex-end',
          }}
        >
          <Button onClick={handleResize}>
            {state.context.modalWidth < 1280 ? 'Expand Video' : 'Shrink Video'}
          </Button>
          <Button onClick={handlePlayPause}>
            {state.matches('playing') ? 'Pause' : 'Play'}
          </Button>
        </div>
      </Modal>
    </div>
  );
};

export default App;
